<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <el-row :gutter="20">
          <el-col :span="5">
            <el-form-item :label="$t('order.createDate')" prop="startDate">
              <ct-date-picker
                :begin.sync="qryParam.startDate"
                :end.sync="qryParam.endDate"
              >
              </ct-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('order.busType')" prop="batchType">
              <ct-async-select
                v-model="qryParam.batchTypes"
                multiple
                collapse-tags
                :placeholder="selectPh('order.busType')"
                url="/order/qryBatTemplateByCond"
                method="post"
                label-key="tmplName"
                value-key="tmplId"
              ></ct-async-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('order.batchStatus')" prop="batchStatus">
              <ct-select
                v-model="qryParam.batchStatus"
                :placeholder="selectPh('order.batchStatus')"
                :options="batchTaskStatus"
              >
              </ct-select>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <!-- <el-dropdown split-button type="primary" @click="qryBatchTaskList(false)"
                  >查询
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="showUpload = true"
                      >批量查询</el-dropdown-item
                    >
                  </el-dropdown-menu>
                </el-dropdown> -->
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryBatchTaskList(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- <div class="tool-button-group">
          <BatchExport
            :query-id-list="[1028, 1028, 1016]"
            :params="simListExportParam"
            :show-msg="tableData.length === 0"
            :export-origin="exportOrigin"
            show-prod="productInfo"
          ></BatchExport>
        </div> -->
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        height="100%"
        width="100%"
        :data="tableData"
        :border="true"
      >
        <el-table-column
          prop="batchTaskId"
          :label="$t('order.batchTaskId')"
          show-overflow-tooltip
          min-width="70px"
        >
          <!--          <template slot-scope="scope">-->
          <!--            <a-->
          <!--              class="cor-blue"-->
          <!--              @click="exportDetail(scope.row.batchTaskId, null, scope.row.uploadFileType)"-->
          <!--              >{{ scope.row.batchTaskId }}</a-->
          <!--            >-->
          <!--          </template>-->
        </el-table-column>
        <el-table-column
          prop="batchTemplateId"
          :label="$t('order.busType')"
          column-key="batchTypes"
          min-width="70px"
          show-overflow-tooltip
          :formatter="rowFormatter"
        >
        </el-table-column>
        <el-table-column
          prop="statusName"
          :label="$t('order.batchStatus')"
          column-key="batchStatus"
          :filter-multiple="false"
          min-width="70px"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <span v-if="scope.row.statusCd == '1600'"
              ><i
                class="inline-block w6 h6 br3 mar-r10 vam"
                style="background: #33cc34"
              ></i
              >{{ formatStatusCd(scope.row.statusCd) }}</span
            >
            <span v-else-if="scope.row.statusCd == '1200'"
              ><i
                class="inline-block w6 h6 br3 mar-r10 vam"
                style="background: #fb4910"
              ></i
              >{{ formatStatusCd(scope.row.statusCd) }}</span
            >
            <span v-else-if="scope.row.statusCd == '1000'"
              ><i
                class="inline-block w6 h6 br3 mar-r10 vam"
                style="background: #ff9500"
              ></i
              >{{ formatStatusCd(scope.row.statusCd) }}</span
            >
            <span v-else
              ><i
                class="inline-block w6 h6 br3 mar-r10 vam"
                style="background: #007aff"
              ></i
              >{{ formatStatusCd(scope.row.statusCd) }}</span
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="statusResult"
          :label="$t('order.statusResult')"
          min-width="150px"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <span v-if="scope.row.statusCd != '1200'">
              {{ $t('order.success')
              }}<a
                class="cor-green"
                @click="
                  showBatchDetail(scope.row.batchTaskId, 'S0C', scope.row.uploadFileType)
                "
                >{{ scope.row.statusResult | formatResult(0) }}</a
              >, {{ $t('order.fail')
              }}<a
                class="cor-red"
                @click="
                  showBatchDetail(scope.row.batchTaskId, 'S0E', scope.row.uploadFileType)
                "
                >{{ scope.row.statusResult | formatResult(1) }}</a
              >, {{ $t('order.doing')
              }}<a
                class="cor-blue"
                @click="
                  showBatchDetail(scope.row.batchTaskId, 'S0W', scope.row.uploadFileType)
                "
                >{{ scope.row.statusResult | formatResult(2) }}</a
              >, {{ $t('order.wait')
              }}<a
                class="cor-orange"
                @click="
                  showBatchDetail(scope.row.batchTaskId, 'S0A', scope.row.uploadFileType)
                "
                >{{ scope.row.statusResult | formatResult(3) }}</a
              >
            </span>
            <span v-else>{{ scope.row.statusResult }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="createDate"
          :label="$t('order.createDate')"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="finishDate"
          :label="$t('order.finishDate')"
          show-overflow-tooltip
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <DetailDialog
      :visible.sync="detailDialogVisible"
      :modify-form="modifyForm"
      @exportDetail="exportDetail"
    ></DetailDialog>
  </div>
</template>
<script>
import axios from 'axios'
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
import DetailDialog from './dialogs/DetailDialog'
import moment from 'moment'
import qryBatTemplateByCond from '@/mock/modules/order/qryBatTemplateByCond'
const defaultForm = {
  batchTypes: [],
  batchStatus: [],
  startDate: '',
  endDate: '',
}
export default {
  components: {
    DetailDialog,
  },
  filters: {
    formatResult: function (val, index) {
      const statusList = val.split(',')
      return statusList[index]
    },
  },
  mixins: [langMixin],
  data() {
    return {
      detailDialogVisible: false,
      modifyForm: {},
      showUpload: false,
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      statusMap: {
        1000: ['1000'],
        1100: ['1100', '1300', '1400', '1500'],
        1200: ['1200'],
        1300: ['1600'],
      },
      tableData: [],
      modifyTitle: '新建',
      modifyParamDialogVisible: false,
      statusCd: '1000',
      qryParam: { ...defaultForm },
      querySupplierIspListRes: [],
      exportOrigin: 'simList',
      pickerOptions: {
        disabledDate(time) {
          return (
            time.getTime() < Date.now() - 30 * 24 * 60 * 60 * 1000 ||
            time.getTime() > Date.now()
          )
        },
      },
    }
  },
  computed: {
    batchTaskStatus() {
      return [
        { value: 1000, label: this.$t('order.batchStatus1000') },
        { value: 1100, label: this.$t('order.batchStatus1100') },
        { value: 1200, label: this.$t('order.batchStatus1200') },
        { value: 1300, label: this.$t('order.batchStatus1300') },
      ]
    },
  },
  mounted() {
    this.qryBatchTaskList(false)
  },
  methods: {
    rowFormatter(row, col, val) {
      if (col.property === 'batchTemplateId') {
        return qryBatTemplateByCond[localStorage.getItem('language')].data.find(
          (item) => item.tmplId == val
        ).tmplName
      }
    },
    formatStatusCd(statusCd) {
      let targetKey = null
      // 遍历 statusMap 的每一个 key-value 对
      for (const key in this.statusMap) {
        if (Object.prototype.hasOwnProperty.call(this.statusMap, key)) {
          // 检查 statusCd 是否存在于当前 key 的 value 数组中
          if (this.statusMap[key].includes(statusCd)) {
            // 如果存在，则返回当前 key
            targetKey = key
          }
        }
      }
      return (
        this.batchTaskStatus.find((item) => item.value == targetKey)?.label || statusCd
      )
    },
    async qryBatchTaskList(pageChangeFlag) {
      try {
        this.loading = true
        if (!pageChangeFlag) {
          this.page.pageIndex = 1
          this.page.pageSize = 10
        }
        const batchStatus = this.statusMap[this.qryParam.batchStatus]
        const params = { ...this.qryParam, ...this.page, batchStatus }
        const res = await fetch('/order/qryBatchTaskList', params, {
          method: 'post',
        })
        this.tableData = res.list || []
        this.page.total = res.total
        this.loading = false
      } catch (e) {
        this.loading = false
      }
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryBatchTaskList(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryBatchTaskList(true)
    },
    // 重置
    reset() {
      this.qryParam = { ...defaultForm }
      this.qryBatchTaskList(false)
    },
    showBatchDetail(batchTaskId, statusCd, typeName) {
      this.modifyForm.batchTaskId = batchTaskId
      this.modifyForm.batchListStatus = statusCd
      this.modifyForm.typeName = typeName
      this.detailDialogVisible = true
    },
    exportDetail(batchTaskId, statusCd, typeName) {
      console.log(batchTaskId, statusCd, typeName)
      let url = process.env.VUE_APP_REQUEST_BASE_API + '/order/export'
      let params = { batchTaskId: batchTaskId, batchListStatus: statusCd }
      const selectedBatch = this.tableData.find(
        (batch) => batch.batchTaskId === batchTaskId
      )
      if (selectedBatch.templateName === '批量写号') {
        url = '/order/exportESIMWriteNum'
        params = { batchTaskId: batchTaskId }
      }
      const fileName = selectedBatch.templateName
      axios({
        url: url,
        method: 'post',
        data: params,
        responseType: 'blob',
      }).then((res) => {
        const blob = new Blob([res.data], {
          type: 'application/vnd.ms-excel;charset=UTF-8',
        })
        const downloadElement = document.createElement('a')
        const href = window.URL.createObjectURL(blob)
        downloadElement.href = href
        downloadElement.download =
          fileName + '_' + moment(Date()).format('YYYYMMDDHHmmss') + '.' + typeName
        document.body.appendChild(downloadElement)
        downloadElement.click()
        setTimeout(function () {
          document.body.removeChild(downloadElement) // 下载完成移除元素
          window.URL.revokeObjectURL(href) // 释放掉blob对象
        }, 1000)
      })
    },
  },
}
</script>
