<template>
  <el-dialog
    :title="$t('order.doTitle')"
    :visible.sync="v"
    :before-close="handleClose"
    width="840px"
    :close-on-click-modal="false"
    append-to-body
  >
    <el-form ref="paramFormRef" size="small" :model="paramForm">
      <el-row :gutter="20">
        <el-col :span="5">
          <el-form-item :label="$t('order.batchTaskId')" prop="batchTaskId">
            <el-input v-model="paramForm.batchTaskId" disabled></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item :label="$t('order.accNum')" prop="accNum">
            <el-input
              v-model="paramForm.accNum"
              :placeholder="inputPh('order.accNum')"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="14">
          <el-form-item label-width="0">
            <div class="search-button-group">
              <el-button type="primary" @click="qryBatchDetail(false)">{{
                $t('common.query')
              }}</el-button>
              <el-button
                class="fr"
                :disabled="tableData.length === 0"
                @click="exportDetail()"
                >{{ $t('common.export') }}
              </el-button>
            </div>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-table
      ref="table"
      v-loading="loading"
      height="100%"
      width="100%"
      :data="tableData"
      :border="true"
    >
      <el-table-column
        prop="listId"
        :label="$t('order.listId')"
        min-width="130px"
        show-overflow-tooltip
      ></el-table-column>
      <!-- <el-table-column prop="iccid" label="ICCID" show-overflow-tooltip></el-table-column> -->
      <el-table-column
        prop="accNum"
        label="MSISDN"
        min-width="130px"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="statusResult"
        :label="$t('order.statusName')"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
    <el-pagination
      background
      :current-page="page.pageIndex"
      align="right"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="page.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </el-dialog>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '',
    },
    modifyForm: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      paramForm: {},
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      paramRules: {
        userCode: [
          { required: true, message: '请输入', trigger: 'blur' },
          { validator: this.validateUserCode, trigger: 'blur' },
        ],
        userName: [
          { required: true, message: '请输入', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' },
        ],
        email: [
          { required: true, message: '请输入', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur'] },
        ],
        mobile: [
          { required: true, message: '请输入', trigger: 'blur' },
          { validator: this.validatePhone, trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: this.validatePassword, trigger: 'blur' },
        ],
        passwordCheck: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: this.validatePassword2, trigger: 'blur' },
        ],
        custId: [{ required: true, message: '请选择', trigger: 'blur' }],
        roleId: [{ required: true, message: '请选择', trigger: 'blur' }],
      },
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  watch: {
    visible(val) {
      if (val) {
        this.paramForm = { ...this.modifyForm }
        this.qryBatchDetail()
      }
    },
  },
  methods: {
    exportDetail() {
      if (this.tableData.length === 0) {
        this.$message({
          type: 'info',
          message: '暂无数据，无法导出',
        })
        return
      }
      this.$emit(
        'exportDetail',
        this.paramForm.batchTaskId,
        this.paramForm.batchListStatus,
        this.paramForm.typeName
      )
    },
    async qryBatchDetail() {
      this.loading = true
      const params = { ...this.paramForm, ...this.page }
      const res = await fetch('/order/qryBatchDetail', params, {
        method: 'post',
      }).finally(() => (this.loading = false))
      this.tableData = res.list || []
      this.page.total = res.total
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryBatchDetail()
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryBatchDetail()
    },
    cancel() {
      this.v = false
      this.$refs.paramFormRef.clearValidate()
    },
    handleClose() {
      this.cancel()
    },
    validatePhone(rule, value, callback) {
      if (!/^\d{1,20}$/.test(value)) {
        callback(new Error('手机号码格式错误'))
      }
      callback()
    },
    validateUserCode(rule, value, callback) {
      if (!value) {
        callback(new Error('请输入账号'))
        return
      }
      if (value.length > 20 || !/^[\da-zA-Z_]+$/g.test(value)) {
        callback(new Error('请输入6位到32位字母、数字、下划线组合'))
        return
      }
      callback()
    },
    validatePassword(rule, value, callback) {
      if (!value) {
        callback(new Error('请输入登录密码'))
        return
      }
      const pattern =
        /^(?![A-z0-9]+$)(?=.[^%&',;=?$\x22])(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,20}$/

      if (!pattern.test(value)) {
        callback(new Error('请输入8位到20位包含英文大小写,数字,特殊字符'))
        return
      }
      callback()
    },
    validatePassword2(rule, value, callback) {
      if (!value) {
        callback(new Error('请输入登录密码'))
        return
      }
      const pattern =
        /^(?![A-z0-9]+$)(?=.[^%&',;=?$\x22])(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,20}$/

      if (!pattern.test(value)) {
        callback(new Error('请输入8位到20位包含英文大小写,数字,特殊字符'))
        return
      }
      if (value != this.paramForm.password) {
        callback(new Error('密码不一致'))
        return
      }
      callback()
    },
  },
}
</script>
<style lang="scss" scoped>
.search-button-group {
  width: 100%;
  white-space: nowrap;
  display: flex;
  align-items: center;
  margin-top: 32px;
  & > :first-child {
    margin-left: auto;
  }
  .el-button {
    height: 32px;
  }

  .el-dropdown {
    margin-right: 10px;
  }
}
</style>
