<template>
  <div class="container">
    <div class="container-form">
      <div v-for="type in types" :key="type.batchType">
        <ct-section-title :title="type.batchTypeName"> </ct-section-title>
        <div class="summary">
          <BatchTaskItem
            v-for="item in type.batTmpls"
            :key="item.tmplId"
            :item="item"
            @upload="upload"
          />
        </div>
      </div>
    </div>
    <BatchUpload
      :visible.sync="showUpload"
      :query-id="queryId"
      :is-xls="true"
      download-url="/order/download/"
      upload-url="/order/upload"
      :max-file-size="10 * 1024 * 1024"
      :title="tmplName"
    >
      <template #tip>
        1、{{ $t('batchUpload.uploadTip1xls') }}<br />
        2、{{ $t('batchUpload.uploadTip2') }}<br />
        3、{{ $t('batchUpload.uploadTip3') }}<br />
        4、{{ $t('batchUpload.uploadTipRule') }}<br />
        5、{{ $t('batchUpload.uploadTipSimMag', { maxNum }) }}<br />
        6、{{ $t('batchUpload.uploadTip5') }}
      </template>
    </BatchUpload>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import BatchUpload from '@/components/BatchUpload'
import BatchTaskItem from './components/BatchTaskItem'
export default {
  components: {
    BatchUpload,
    BatchTaskItem,
  },
  data() {
    return {
      showUpload: false,
      powerSearch: false,
      types: [],
      queryId: '',
      tmplName: '',
      tmplCode: '',
      maxNum: '_',
    }
  },

  mounted() {
    this.qryBatchTypeList()
  },
  methods: {
    upload(item) {
      this.showUpload = true
      this.queryId = item.tmplId
      this.tmplName = item.tmplName
      this.tmplCode = item.tmplCode
      this.getParamValue()
    },
    async getParamValue() {
      this.maxNum = '_'
      this.maxNum =
        (await fetch(
          '/user/getParamValue',
          { paramCode: this.tmplCode },
          {
            method: 'get',
          }
        )) ?? '_'
    },
    async qryBatchTypeList() {
      this.types = await fetch(
        '/order/qryBatchTypeList',
        {},
        {
          method: 'get',
        }
      )
      this.types = this.types.filter((type) => type.batTmpls && type.batTmpls.length > 0)
      this.types.forEach(
        (type) => (type.batTmpls = type.batTmpls.filter((item) => item.displayFlag === 1))
      )
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
.container {
  background-color: white !important;
  &-form {
    padding: 0px;
    overflow: auto;
  }
}
.summary {
  display: flex;
  padding: 20px 0px;
  min-width: 900px;
}
</style>
