<!--
使用参考：
<batchUpload v-if="showUpload" :visible.sync = "showUpload" :title="batchTitle" :queryId = "queryId" :maxFileSize="10*1024*1024" >
  <template v-slot:tip>
    导入必须是xls文件；<br/>
    只导入EXCEL文件的第一个标签页；<br/>
    文件大小最大为10MB；<br/>
    号码最多10000个。
  </template>
</batchUpload>-->

<template>
  <el-dialog
    :close-on-click-modal="false"
    :title="title"
    :visible.sync="v"
    :append-to-body="true"
    width="840px"
    top="10vh"
  >
    <div v-loading="loading" :element-loading-text="$t('batchUpload.uploading')">
      <p class="download-tip">
        {{ $t('batchUpload.please')
        }}<a class="cor-blue" target="_self" @click="downloadTpl">{{
          $t('batchUpload.downloadTpl')
        }}</a
        >{{ $t('batchUpload.please2') }}
      </p>
      <div class="tip">
        <i class="icon-zhuyi" style="color: orange"></i>
        <p class="upload-tip">
          {{ $t('batchUpload.warmTip') }}<br /><slot name="tip"></slot>
        </p>
      </div>
      <div class="upload-box">
        <el-upload
          ref="upload"
          class="upload-wrap"
          drag
          accept=".xls,.xlsx"
          :action="action"
          :data="extraParam"
          :show-file-list="true"
          :multiple="false"
          :auto-upload="false"
          :file-list="fileList"
          :on-change="handlerChange"
          :on-remove="handleRemove"
          :on-progress="handlerProgress"
          :on-success="handlerSuccess"
          :on-error="handlerError"
          :before-upload="beforeUpload"
        >
          <div class="upload_file_choose">
            <i class="el-icon-upload cor-blue"></i>
            <div class="upload_file_choose_text">
              {{ $t('batchUpload.drag1') }}<em>{{ $t('batchUpload.drag2') }}</em
              ><br /><span class="support-tip">{{ supportFormat }}</span>
            </div>
          </div>
        </el-upload>
      </div>
      <div class="download-template">
        <el-button type="text" icon="icon-xiazai1" @click="downloadTpl"
          >{{ $t('batchUpload.downloadTpl') }}
        </el-button>
      </div>
    </div>
    <span slot="footer">
      <el-button type="primary" :disabled="disableSubmit" @click="submit">{{
        $t('batchUpload.submit')
      }}</el-button>
      <el-button @click="v = false">{{ $t('common.cancel') }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  components: {},
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    queryId: {
      type: [String, Number],
      default: '',
    },
    downloadUrl: {
      type: String,
      default: '/batch/downloadTpl/',
    },
    uploadUrl: {
      type: String,
      default: '/batch/import',
    },
    maxFileSize: {
      type: Number,
      default: 10 * 1024 * 1024,
    },
    title: {
      type: String,
      default: '',
    },
    param: {
      type: Object,
      default: () => {},
    },
    isXls: {
      type: Boolean,
      default: false,
    },
    // 是否是规则处的批量操作
    isRuleBatch: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      uploaded: false,
      loading: false,
      fileName: '',
      fileSize: 0,
      fileList: [],
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
        if (!value) {
          this.fileList = []
        }
      },
    },
    supportFormat() {
      return this.$t('batchUpload.drag3') + ' ' + (this.isXls ? '.xls' : '.xlsx')
    },
    action() {
      return process.env.VUE_APP_REQUEST_BASE_API + this.uploadUrl
    },
    isSpecial() {
      // 账户详情批量上传 订单批量上传
      return this.uploadUrl === '/simCard/upload' || this.uploadUrl === '/order/upload'
    },
    disableSubmit() {
      return !this.fileName || this.loading
    },
    extraParam() {
      if (this.isSpecial || this.isRuleBatch) {
        return { templateId: this.queryId, ...this.param }
      }
      return { queryId: this.queryId, ...this.param }
    },
    showPreview() {
      return !this.uploaded && this.fileName
    },
    fileSizeFormat() {
      if (this.fileSize > 1024 * 1024) {
        return (this.fileSize / 1024 / 1024).toFixed(2) + 'MB'
      } else {
        return (this.fileSize / 1024).toFixed(2) + 'KB'
      }
    },
  },
  methods: {
    downloadTpl() {
      window.open(this.downloadUrl + this.queryId, '_self')
    },
    handlerProgress(event, file, fileList) {
      this.loading = true
      this.fileName = file.name
      this.fileSize = file.size
    },
    handlerChange(file, fileList) {
      console.log('handlerChange', file, fileList)
      if (fileList.length > 1) {
        fileList.splice(0, 1)
      }
      if (!this.uploaded) {
        this.fileName = file.name
        this.fileSize = file.size
      }
    },
    handleRemove(file, fileList) {
      this.resetUpload()
    },
    handlerSuccess(response, file, fileList) {
      if (response.data && response.data.length > 0) {
        this.errAlert(response.data)
        return
      }
      if (response.code != '0' && response.message) {
        this.errAlert(response.message)
        return
      }
      this.uploaded = true
      const timer = setTimeout(() => {
        this.loading = false
        clearTimeout(timer)
      }, 200)

      if (this.isSpecial) {
        this.$message.success(this.$t('batchUpload.subtmitSuccess'))
        this.uploaded = false
        this.v = false
      } else {
        const self = this
        const h = this.$createElement
        this.$msgbox({
          title: this.$t('batchUpload.subtmitSuccess'),
          type: 'success',
          center: true,
          message: h('p', { class: 'fs12' }, [
            this.$t(this.isRuleBatch ? 'batchUpload.task2' : 'batchUpload.task'),
            h(
              'a',
              { class: 'cor-blue', on: { click: self.viewProgress } },
              this.$t('batchUpload.process')
            ),
          ]),
          showCancelButton: false,
          confirmButtonText: this.$t('batchUpload.know'),
        })
          .then(() => {
            this.uploaded = false
            this.v = false
          })
          .catch(() => {})
      }
    },
    resetUpload() {
      this.$refs.upload.clearFiles()
      this.uploaded = false
      this.fileName = ''
      this.fileSize = 0
    },
    showErrMsg(errMsg) {
      this.$msgbox({
        title: this.$t('batchUpload.subtmitFail'),
        type: 'warning',
        center: true,
        message: errMsg,
        showCancelButton: false,
        confirmButtonText: this.$t('batchUpload.know'),
      })
    },
    handlerError(err) {
      const timer = setTimeout(() => {
        this.loading = false
        clearTimeout(timer)
      }, 200)
      let errMsg = this.$t('batchUpload.systemError')
      if (err.message) {
        const parse = JSON.parse(err.message)
        if (parse && parse.message) {
          errMsg += '，' + parse.message
        }
      }
      this.showErrMsg(errMsg)
      this.$nextTick(this.resetUpload)
    },
    errAlert(errMsgList) {
      this.$nextTick(this.resetUpload)
      const timer = setTimeout(() => {
        this.loading = false
        clearTimeout(timer)
      }, 200)
      const h = this.$createElement
      const msg = h('div', { class: 'error-msg-box' }, [
        ...(function () {
          const arr = []
          errMsgList.forEach((item) => {
            arr.push(h('p', { class: 'cor-red' }, item))
          })
          return arr
        })(),
      ])
      this.showErrMsg(msg)
    },
    toReload() {
      this.resetUpload()
    },
    submit() {
      this.$refs.upload.submit()
    },
    beforeUpload(file) {
      if (this.fileSize > this.maxFileSize) {
        let maxFileSizeView = ''
        if (this.maxFileSize > 1024 * 1024) {
          maxFileSizeView = this.maxFileSize / 1024 / 1024 + 'MB'
        } else {
          maxFileSizeView = this.maxFileSize / 1024 + 'KB'
        }
        this.$message.error(this.$t('batchUpload.fileSize') + maxFileSizeView)
        return false
      }
      if (
        file.type !== 'application/vnd.ms-excel' &&
        file.type !== 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      ) {
        this.$message.error(this.$t('batchUpload.fileType'))
        return false
      }
      return true
    },
    viewProgress() {
      this.uploaded = false
      this.v = false
      this.$msgbox.close()
      if (this.isRuleBatch) {
        this.$router.push({ path: '/order/orderBatch', query: { activeTab: '2' } })
      } else {
        this.$router.push('/common/download')
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.download-tip {
  color: #333;
  margin-bottom: 10px;
}
.tip {
  background: #f3f5f7;
  font-size: 14px;
  color: #666;
  line-height: 30px;
  border-radius: 4px;
  padding: 20px;
  margin-top: 15px;
  margin-bottom: 20px;
  position: relative;

  .icon-zhuyi {
    position: absolute;
  }
  .upload-tip {
    margin-left: 20px;
    &::first-line {
      color: #333;
    }
  }
}
.support-tip {
  font-weight: 400;
  font-size: 14px;
  color: #999999;
}
::v-deep .el-upload {
  width: 100%;
  .el-upload-dragger {
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.upload_file_choose {
  i {
    color: #2b6ffb;
  }

  &_text {
    line-height: 25px;
  }
}
.download-template {
  display: flex;
  ::v-deep .el-button {
    margin-left: auto;
  }
  margin: 12px 0;
}
::v-deep .el-dialog__footer {
  border-top: 1px solid #d6d6d6;
  padding-top: 20px;
  padding-bottom: 20px;
  // .el-button:first-child {
  //   width: 86px;
  // }
  // .el-button:last-child {
  //   width: 60px;
  // }
}
.error-msg-box {
  border: 1px solid #cad4e0;
  padding: 15px;
  max-height: 230px;
  overflow: auto;
  margin-top: 10px;
  p {
    text-align: left;
    line-height: 1.2;
    font-size: 12px;
  }
}
</style>
